import { Line, mixins } from 'vue-chartjs'

export default {
  extends: Line,
  mixins: [mixins.reactiveProp],
  props: ['chartData', 'options', 'diffType'],
  computed: {
    unit () {
      return this.diffType
    }
  },
  watch: {
    chartData (val, oldVal) {
      // console.log('updating chart')
      this.renderChart(this.chartData, {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          labels: {
            fontSize: 16,
            usePointStyle: true
          }
        },
        tooltips: {
          bodyFontSize: 14,
          mode: 'index',
          intersect: true
        },
        hover: {
          mode: 'nearest',
          intersect: true
        },
        scales: {
          xAxes: [{
            type: 'time',
            scaleLabel: {
              display: true,
              labelString: 'Time',
              fontColor: '#333'

            },
            time: {
              unit: this.unit
            }
          }],
          yAxes: [{
            scaleLabel: {
              display: true,
              lablabelStringel: 'Ratings',
              fontColor: '#333'
            }
          }]
        }
      })
    },
    unit (val, oldVal) {
      if (val !== oldVal) {
        this.renderChart(this.chartData, {
          responsive: true,
          maintainAspectRatio: false,
          legend: {
            labels: {
              fontSize: 16,
              usePointStyle: true
            }
          },
          tooltips: {
            bodyFontSize: 14,
            mode: 'index',
            intersect: true
          },
          hover: {
            mode: 'nearest',
            intersect: true
          },
          scales: {
            xAxes: [{
              type: 'time',
              scaleLabel: {
                display: true,
                label: 'Time'
              },
              time: {
                unit: this.unit
              }
            }],
            yAxes: [{
              scaleLabel: {
                display: true,
                label: 'Ratings'
              }
            }]
          }
        })
      }
    }
  },
  mounted () {
    // this.chartData is created in the mixin.
    // If you want to pass options please create a local options object
    // console.log(this)
    this.renderChart(this.chartData, {
      responsive: true,
      maintainAspectRatio: false,
      legend: {
        labels: {
          fontSize: 16,
          usePointStyle: true
        }
      },
      tooltips: {
        bodyFontSize: 14,
        mode: 'index',
        intersect: true
      },
      hover: {
        mode: 'nearest',
        intersect: true
      },
      scales: {
        xAxes: [{
          type: 'time',
          scaleLabel: {
            display: true,
            label: 'Time'
          },
          time: {
            unit: this.unit
          }
        }],
        yAxes: [{
          scaleLabel: {
            display: true,
            label: 'Ratings'
          }
        }]
      }
    })
  }
}
